import Layout from '../components/Layout';
import Link from 'next/link';

const PostLink = ({title}) => (
  <li>
    <Link as='/react-post' href={`/post?title=${title}`}>
      <a>{title} </a>
    </Link>
  </li>
)

export default () => (
  <Layout title='Blog'>
    <ul>
      <PostLink title='React'/>
      <PostLink title='Angular'/>
      <PostLink title='flutter'/>
      <PostLink title='vue'/>
    </ul>
  </Layout>
);
